import React from 'react';
import { graphql, PageProps } from 'gatsby';
import blogData from '../data/blogData';

import Box from '@mui/joy/Box';
import Avatar from '@mui/joy/Avatar';
import Button from '@mui/joy/Button';
import IconButton from '@mui/joy/IconButton';
import JoyTypography from '@mui/joy/Typography';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import AppContainer from '../layouts/AppContainer';
import Tag from '../components/Tag';
import Image from '../components/Image';
import Typography from '../components/Typography';

interface AdaptiveStyle {
  cover: any;
  contentBody: any;
  contentCard: any;
}

interface AdaptiveStyles {
  [_: string]: AdaptiveStyle;
}

const adaptiveStyles: AdaptiveStyles = {
  xs: {
    cover: {},
    contentBody: {},
    contentCard: {
      padding: '16px',
    },
  },
  md: {
    cover: {
      overflow: 'hidden',
      borderRadius: '20px',
      position: 'absolute',
      left: 0,
      right: 0,
      top: 0,
      zIndex: -1,
    },
    contentCard: {
      width: '96%',
      padding: '16px',
      maxWidth: '800px',
      marginLeft: 'auto',
      marginRight: 'auto',
    },
    contentBody: {
      display: 'inline-block',
      width: '100%',
      marginTop: '280px',
      padding: '24px',
      minHeight: '840px',
      background: 'white',
      borderRadius: '16px',
    },
  },
};

function adaptiveStyle(name: keyof AdaptiveStyle) {
  const result: any = {};

  for (const size in adaptiveStyles) {
    const style = adaptiveStyles[size][name];
    for (const varname in style) {
      result[varname] ||= {};
      result[varname][size] = style[varname];
    }
  }

  console.log(name, result);
  return result;
}

export default function BlogPostTemplate({ data }: PageProps<Queries.BlogPostPageQuery>) {
  const markdownRemark = data.markdownRemark!;
  const { frontmatter, html } = markdownRemark!;
  const { title, date, cover, tags } = frontmatter!;

  return (
    <AppContainer title={title!} isArticle={true}>
      {frontmatter?.cover && (
        <Box
          sx={{
            position: 'relative',
          }}
        >
          <Box sx={adaptiveStyle('cover')}>
            <Image
              src={cover!}
              sx={{
                objectFit: 'cover',
                height: '500px',
              }}
            ></Image>
          </Box>
        </Box>
      )}
      <Box>
        <Box sx={adaptiveStyle('contentCard')}>
          <Box sx={adaptiveStyle('contentBody')}>
            <h1
              style={{
                fontSize: '1.6rem',
              }}
            >
              {title!}
            </h1>
            <Box
              sx={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                flexWrap: 'wrap',
                gap: 2,
              }}
            >
              <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                <Avatar src={'/avatar.png'} sx={{ borderRadius: 'sm' }} />
                <Box sx={{ ml: 2 }}>
                  <JoyTypography level="body2" textColor="text.primary" mb={0.5}>
                    {blogData.avatar}
                  </JoyTypography>
                  <JoyTypography level="body3" textColor="text.tertiary">
                    {date!}
                  </JoyTypography>
                </Box>
              </Box>
              <Box sx={{ display: 'flex', height: '32px', flexDirection: 'row', gap: 1.5 }}>
                <IconButton size="sm" variant="outlined" color="neutral">
                  <FavoriteBorderIcon />
                </IconButton>
              </Box>
            </Box>

            <Typography>
              <div dangerouslySetInnerHTML={{ __html: html! }}></div>
            </Typography>
            {tags?.length && (
              <Box>
                {tags!.map((name: string | null) => (
                  <Tag name={name || ''}></Tag>
                ))}
              </Box>
            )}
          </Box>
        </Box>
      </Box>
    </AppContainer>
  );
}

export const pageQuery = graphql`
  query BlogPostPage($id: String!) {
    markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        title
        cover
        tags
      }
    }
  }
`;
